@import 'variables';


.main {
    &__about {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &__block1 {
            background: url("../img/who_photo.png") no-repeat center;
            @include photo_block;
            // &__background
            //     max-width: 100vw
            //     min-width: 100vw
            //     min-height: calc( 100vh - 80px )
            //     max-height: calc( 100vh - 80px )
            &__h2 {
                @include photo_block_h2;
                // display: flex
                // justify-content: space-around
                // align-items: center
                // max-height: 500px
 } }                // min-height: 400px
        &__block2 {
            min-height: calc( 100vh - 80px );
            max-height: calc( 100vh - 80px );
            position: relative;
            display: flex;
            justify-content: space-around;
            align-items: center;
            justify-content: center;
            max-width: calc( 100vw - 100px );
            min-width: calc( 100vw - 100px);
            &__text {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                &__h2 {
                    font-family: 'mainBold';
                    font-size: 48px;
                    text-transform: uppercase;
                    margin-bottom: 40px;
                    &__underline {
                        @include underline; } }
                &__p {
                    font-family: 'mainLight';
                    font-size: 24px;
                    line-height: 120%;
                    max-width: 410px; } } }
        &__block3 {
            min-height: calc( 100vh - 80px );
            max-height: calc( 100vh - 80px );
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: calc( 100vw - 100px );
            min-width: calc( 100vw - 100px);
            &__text {
                display: flex;
                min-width: -webkit-fill-available;
                justify-content: space-around;
                &__column {
                    max-width: 450px;
                    &__h2 {
                        font-family: 'mainBold';
                        font-size: 36px;
                        margin-bottom: 35px;
                        &__underline {
                            @include underline; } }
                    &__p {
                        font-family: 'mainLight';
                        font-size: 20px;
                        line-height: 150%;
                        &:first-child {
                            margin-bottom: 20px; } } } } }
        &__block4 {
            // min-height: calc( 100vh - 80px )
            // max-height: calc( 100vh - 80px )
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            max-width: calc( 100vw - 100px );
            min-width: calc( 100vw - 100px);
            margin: 100px 0px;
            &__text {
                max-width: 1000px;
                margin-left: 50px;
                &__h2 {
                    font-family: 'mainBold';
                    font-size: 36px;
                    margin-bottom: 35px;
                    &__underline {
                        @include underline; } }
                &__p {
                    font-family: 'mainLight';
                    font-size: 20px;
                    line-height: 150%; } } } } }
